.callback {
  position: relative;
  float: left;
  width: 100%;
  height: 100%;
  background-image: url("../img/callback-bg.png");
  background-repeat: no-repeat;
  padding-bottom: 135px;

  @include breakpoint(max-width 1024px) {
    padding-bottom: 70px;
  }

  @include breakpoint(max-width 400px) {
    background-color: #e2eaf7;
    background-image: none;
  }

  &__title {
    position: relative;
    color: #60759d;
    font-family: $CeraRoundPro;
    font-size: 36px;
    font-weight: 500;
    line-height: 54px;
    text-align: center;
    margin-top: 150px;

    @include breakpoint(max-width 1024px) {
      margin-top: 50px;
    }
  }

  &__descr {
    position: relative;
    color: #60759d;
    font-family: $CeraRoundPro;
    font-size: 24px;
    font-weight: 500;
    line-height: 36px;
    margin-top: 60px;
    text-align: center;
  }

  &__email-form {
    position: relative;
    width: 50%;
    margin: 0 auto;
    margin-top: 55px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }

  &__email {
    width: 50%;
    border: 1px solid #4f699b;
    border-radius: 30px;
    background-color: transparent;
    padding: 20px 20px;

    @include breakpoint(max-width 1024px) {
      width: 100%;
      margin-bottom: 10px;
    }
  }

  &__submit {
    background-color: #e70067;
    border-radius: 30px;
    padding: 20px 35px;
    color: #fdfffc;
    font-family: $CeraRoundPro;
    font-size: 16px;
    font-weight: 700;
    border: none;

    &:hover{
      background-color: #c9005a;
      border-radius: 30px;
      box-shadow: 0 6px 33.2px 6.8px rgba(231, 0, 103, 0.36);
    }
  }
}