.footer{
  position: relative;
  float: left;
  width: 100%;
  height: 100%;
  background-color: #39649d;

  &__logo{
    position: relative;
    float: left;
    margin-top: 60px;

    @include breakpoint(max-width 400px){
     width: 100%;
      text-align: center;
    }
  }

  &__menu{
    position: relative;
    float: left;
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    margin-top: 60px;
    padding-left: 190px;
    padding-bottom: 50px;

    @include breakpoint(max-width 1024px){
      padding-left: 30px;
      flex-direction: column;
    }

      li{
        margin-left: 85px;

          a{
            color: white;
            font-family: $CeraRoundPro;
            font-size: 16px;
            font-weight: 400;
            line-height: 24px;

            &:hover{
            color: #8994A0;
          }
          }
      }
  }

  &__menu li:first-child{
    margin-left: 0;

    @include breakpoint(max-width 1024px){
      margin-left: 85px;
    }
  }

  &__line{
    position: relative;
    float: left;
    width: 100%;
    border-bottom: 1px solid #335b91;
    margin-bottom: 35px;
  }

  &__copyright{
    position: relative;
    float: left;
    width: 100%;
    text-align: center;
    font-family: $CeraRoundPro;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    color: #e3e7f2;
    margin-bottom: 30px;

      &-color{
        padding-left: 5px;
        color: #e70067;
      }
  }
}